.expertise {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  border-radius: 25px;
  overflow: hidden;
  margin: 0 auto;
  padding: 0 54px;
  width: 100%;

  @include Mobile {
    justify-content: center;
    padding: 0 20px;
  }

  &__container {
    margin-top: 128px;
    display: flex;
    flex-direction: column;
    width: 100%;

    @include Mobile {
      margin-top: 72px;
    }
  }

  &__title {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 52px;
    line-height: 150%;
    text-align: center;
    justify-content: center;

    display: flex;
    align-items: center;

    color: $mainTitle;
  }

  &__photo {
    width: 184px;
    height: 166px;
    border-radius: 10px;
    transition: all 0.3s;

    &:hover {
      transform: scale(1.1);
      box-shadow: 5px 9px 18px $mainBlue;
    }
  }

  &__items {
    display: flex;
    justify-content: space-around;

    @include Mobile {
      flex-wrap: nowrap;
      flex-direction: column;
    }

    @include TheDesign {
      margin-bottom: 155px;
      justify-content: center;
    }

    @include Notebook {
      margin-bottom: 155px;
    }

    @include Tablet {
      margin-bottom: 155px;
    }

    @include FullHD {
      justify-content: center;
      margin-bottom: 155px;
    }

    @include LargeScreens {
      justify-content: center;
      margin-bottom: 155px;
    }
  }
}

.expertise__card {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 64px;
  box-sizing: content-box;

  @include Mobile {
    padding-top: 56px;

    &:last-child {
      padding-bottom: 56px;
    }
  }

  @include Tablet {
    padding-right: 60px;

    &:last-child {
      padding-right: 0;
    }
  }

  @include Notebook {
    padding-right: 180px;

    &:last-child {
      padding-right: 0;
    }
  }

  @include TheDesign {
    padding-right: 168px;

    &:last-child {
      padding-right: 0;
    }
  }

  @include FullHD {
    margin-right: 177px;

    &:last-child {
      margin-right: 0;
    }
  }

  @include LargeScreens {
    margin-right: 177px;

    &:last-child {
      margin-right: 0;
    }
  }

  @include Mobile {
    // margin-top: 56px;
    // margin-bottom: 0px;
  }

  .card__title {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;

    color: $mainTitle;
    margin-top: 40px;
    margin-bottom: 0;
    margin-right: 0;
    margin-left: 0;
  }

  .card__paragraph {
    font-family: "Open Sans", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 150%;
    text-align: center;
    color: $mainText;
    margin-top: 16px;
    height: 84px;
  }

  @include Mobile {
    // width: 280px;
  }

  @include TheDesign {
    max-width: 264px;
  }

  @include FullHD {
    max-width: 264px;
  }

  @include LargeScreens {
    max-width: 264px;
  }
}
